import Head from 'next/head';
import { useRouter } from 'next/router';
import { Layout, ChevronRight, Zap, Shield, Globe } from 'lucide-react';

export default function LandingPage() {
  const router = useRouter();

  return (
    <>
      <Head>
        <title>Welcome | AdminCMS</title>
      </Head>

      {/* Dark Theme Wrapper */}
      <div className="min-h-screen bg-[#0B0B0F] text-white overflow-hidden relative font-sans selection:bg-purple-500/30 flex flex-col">

        {/* Background Gradients */}
        <div className="absolute top-[-20%] left-[-10%] w-[420px] h-[420px] bg-purple-600/20 rounded-full blur-[120px] pointer-events-none" />
        <div className="absolute bottom-[-20%] right-[-10%] w-[420px] h-[420px] bg-pink-600/10 rounded-full blur-[120px] pointer-events-none" />

        {/* Navigation */}
        <nav className="relative z-10 max-w-7xl mx-auto px-6 py-5 flex justify-between items-center animate-fade-down">
          <div className="flex items-center gap-2 font-bold text-xl tracking-tight">
            <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-pink-500 to-purple-600 flex items-center justify-center">
              <Layout size={18} />
            </div>
            <span>
              Admin<span className="text-purple-400">CMS</span>
            </span>
          </div>

          <button
            onClick={() => router.push('/login')}
            className="text-sm font-medium text-gray-300 hover:text-white transition-colors"
          >
            Login
          </button>
        </nav>

        {/* Hero Section */}
        <main className="relative z-10 flex-grow flex flex-col items-center justify-center text-center px-4">

          {/* Badge */}
          <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-white/5 border border-white/10 text-xs font-medium text-purple-300 mb-6 animate-fade-up">
            <span className="w-2 h-2 rounded-full bg-green-500 animate-pulse"></span>
            v2.0 Live
          </div>

          {/* Heading */}
          <h1 className="text-3xl md:text-5xl font-bold tracking-tight mb-3 max-w-3xl animate-fade-up delay-100">
            Your content, <br />
            <span className="bg-clip-text text-transparent bg-gradient-to-r from-pink-500 via-purple-500 to-indigo-500 animate-gradient">
              your way.
            </span>
          </h1>

          {/* Description */}
          <p className="text-base text-gray-400 max-w-2xl mb-8 leading-relaxed animate-fade-up delay-200">
            A powerful, fully customizable content management system built from scratch.
            Manage your portfolio, blog, and projects with complete control.
          </p>

          {/* CTA */}
          <button
            onClick={() => router.push('/login')}
            className="px-8 py-4 bg-gradient-to-r from-pink-600 to-purple-600 hover:opacity-90 rounded-full font-bold text-white shadow-[0_0_20px_-5px_rgba(168,85,247,0.5)] transition-all transform hover:-translate-y-1 flex items-center gap-2 animate-fade-up delay-300"
          >
            Get Started <ChevronRight size={18} />
          </button>

          {/* Features */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-14 max-w-6xl w-full px-6 animate-fade-up delay-500">
            <FeatureCard icon={<Zap className="text-yellow-400" />} title="Lightning Fast" desc="Optimized for speed and performance." />
            <FeatureCard icon={<Shield className="text-pink-400" />} title="Secure" desc="Enterprise-grade security built-in." />
            <FeatureCard icon={<Globe className="text-blue-400" />} title="API Ready" desc="Connect your content anywhere." />
          </div>
        </main>

        {/* Footer */}
        <footer className="relative z-10 text-gray-500 text-xs text-center py-4 border-t border-white/5 animate-fade-up delay-700">
          Â© 2025 <span className="text-gray-400 font-medium">AdminCMS</span>.  
          All rights reserved. Powered by <span className="text-purple-400">SRP Admin CMS</span>.
        </footer>
      </div>

      {/* Animations */}
      <style jsx global>{`
        @keyframes fade-up {
          from {
            opacity: 0;
            transform: translateY(16px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @keyframes fade-down {
          from {
            opacity: 0;
            transform: translateY(-12px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        .animate-fade-up {
          animation: fade-up 0.7s ease-out forwards;
        }

        .animate-fade-down {
          animation: fade-down 0.6s ease-out forwards;
        }

        .delay-100 { animation-delay: 0.1s; }
        .delay-200 { animation-delay: 0.2s; }
        .delay-300 { animation-delay: 0.3s; }
        .delay-500 { animation-delay: 0.5s; }
        .delay-700 { animation-delay: 0.7s; }
      `}</style>
    </>
  );
}

/* Feature Card */
const FeatureCard = ({ icon, title, desc }) => (
  <div className="p-6 rounded-2xl bg-white/5 border border-white/10 hover:border-purple-500/30 transition-all hover:-translate-y-1 text-left group">
    <div className="w-12 h-12 rounded-lg bg-white/5 flex items-center justify-center mb-4 group-hover:scale-110 transition-transform">
      {icon}
    </div>
    <h3 className="text-lg font-bold mb-2">{title}</h3>
    <p className="text-gray-400 text-sm">{desc}</p>
  </div>
);
